@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;700;900&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar-track {
  background: #181818;
}
::-webkit-scrollbar-thumb {
  background: #888;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

*{
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
}
body{
  background-color: rgb(24, 24, 24);
  color: white;
  height: 100vh;
  overflow: hidden;
}
.app-wrapper{
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.games-wrapper{
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  position: relative;
  overflow-y: scroll;
  height: 100%;
}
.game-card-wrapper{
  flex-basis: 20%;
  flex-grow: 0;
  flex-shrink: 0;
}
.game-card{
    max-width: 200px;
    margin: 20px auto;
    padding: 10px 10px;
}

.game-cover{
    width: 100%;
    min-height: 220px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 4px;
}
.game-name{
    font-size: 16px;
    margin-top: 8px;
    margin-bottom: 4px;
    font-weight: 100;
}
.headerMainWrapper{
    display: flex;
    flex-basis: 20px;
    background-color: #181818;
    min-height: 60px;
    align-items: center;
    justify-content: center;
    padding: 10px;
}
.headerWrapper{
    display: flex;
    align-items:center;
    max-width: 1320px;
    margin: auto;
    width: 100%;
}
.searchBarWrapper{
    position: relative;
    flex-grow:1;
}
.searchBarWrapper > .searchBar{
    background-color:#181818;
    border:3px solid #1f1f1f;
    min-height:40px;
    border-radius:5px;
    padding-left:20px;
    color: white;
    width: 100%;
}
.searchBarWrapper > .searchBar::-webkit-input-placeholder{
    letter-spacing: 1px;
}
.searchBarWrapper > .searchBar:-ms-input-placeholder{
    letter-spacing: 1px;
}
.searchBarWrapper > .searchBar::placeholder{
    letter-spacing: 1px;
}
.logo{
    width: 70px;
    height: 50px;
    margin-right:50px;
}
.searchBarWrapper > .searchLoadingIcon{
    width:20px;
    height:20px;
    position: absolute;
    right: 10px;
    top: 10px;
}
